.navbar{
    
    height: 50px;
    background-color: #003580;
    display: flex;
    justify-content: center;
    cursor: pointer;
   
}
.navbar-container{
    margin: 0px 25px 0px 25px;
    width: 100%;
    max-width: 1024px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo{
    font-weight: 600;
}

.nav-button{
    margin-left: 20px;
    border: none;
    padding:5px 10px;
    cursor: pointer;
    color: #003580;
    border-radius: 20px;
}

