.footer{
    width: 100%;
    max-width: 1024px;
    font-size: 15px;
    margin-left: 120px;
}
.footer-lists{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
}
.footer-list{
    list-style: none;
    padding: 0;
}
.footer-list-item{
    margin-bottom: 10px;
    color: #003580;
    cursor: pointer;
}

.copyright{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    color: #003580;
}